<template>
  <div class="p2">
    <!--增值税纳税申报表附列资料【四】-->
    <div>
      <p style="font-size:20px;height:35px; font-weight:bold;text-align: center;">增值税纳税申报表附列资料（四）</p>
      <p style="font-size:20px;height:35px; font-weight:bold;text-align: center;">（税额抵减情况表）</p>
      <p style="text-align: center;">税款所属时间：{{com.month}}-01日至{{com.month}}-30日</p>
      <p style="width:50%; float:left; height:25px; text-align:left;">纳税人名称：{{com.firm_name}}</p>
      <p style="width:50%; float:left; height:25px; text-align:right;">金额单位：元至角分</p>
      <form>
        <table cellpadding="0" cellspacing="0" border="1" width="100%">
          <tr>
            <td rowspan="2">序号</td>
            <td rowspan="2">抵减项目</td>
            <td>期初余额</td>
            <td>本期发生额</td>
            <td>本期应抵减税额</td>
            <td>本期实际抵减税额</td>
            <td>期末余额</td>
          </tr>
          <tr>
            <td>1</td>
            <td>2</td>
            <td>3=1+2</td>
            <td>4≤3</td>
            <td>5=3-4</td>
          </tr>
          <tr>
            <td>1</td>
            <td>增值税税控系统专用设备费及技术维护费</td>
            <td style="background-color:#ccccff;"><input name="1_1" readonly tabindex="1" value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="1_2" tabindex="2" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="1_3" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="1_4" tabindex="3" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="1_5" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td>2</td>
            <td>分支机构预征缴纳税款</td>
            <td style="background-color:#ccccff;"><input name="2_1" readonly tabindex="4" value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="2_2" tabindex="5" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="2_3" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="2_4" tabindex="6" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="2_5" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td>3</td>
            <td>建筑服务预征缴纳税款</td>
            <td style="background-color:#ccccff;"><input name="3_1" readonly tabindex="7" value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="3_2" tabindex="8" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="3_3" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="3_4" tabindex="9" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="3_5" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td>4</td>
            <td>销售不动产预征缴纳税款</td>
            <td style="background-color:#ccccff;"><input name="4_1" readonly tabindex="10" value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="4_2" tabindex="11" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="4_3" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="4_4" tabindex="12" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="4_5" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td>5</td>
            <td>出租不动产预征缴纳税款</td>
            <td style="background-color:#ccccff;"><input name="5_1" readonly tabindex="13" value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="5_2" tabindex="14" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="5_3" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="5_4" tabindex="15" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="5_5" readonly value="0.00"/></td>
          </tr>
        </table>
      </form>
    </div>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 26,  //当前表格的名称编号
        setting: null,
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        this.setting = this.$store.state.companyCurrent.content.page26
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1(3, 5, [1, 2], 1)
        this.fn2(4, 5)
        this.fn1(5, 5, [3, 4], 2)
      },
      //(
      // q  要修改的值列
      // r  要修改多少行
      // seed 每行参与计算的列
      // type 运算类型
      //    {
      //    1加  a+b
      //    2减  a-b
      //    }
      // )
      fn1(q, r, seed, type) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                if (type == 1) {
                  sum += parseFloat(dom.val())
                } else if (type == 2) {
                  if (j == 0) {
                    sum = parseFloat(dom.val())
                  } else {
                    sum -= parseFloat(dom.val())
                  }
                }
              }
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      //(
      // q  要比较的值列
      // r  要修改多少行
      // a<=b
      // )
      fn2(q, r) {
        for (let i = 1; i <= r; i++) {

          if (J('input[name=' + i + '_3]').val()) {
            var dom1 = parseFloat(J('input[name=' + i + '_3]').val())
          } else {
            var dom1 = 0
          }
          if (J('input[name=' + i + '_' + q + ']').val()) {
            var val = parseFloat(J('input[name=' + i + '_' + q + ']').val())
          } else {
            var val = 0
          }
          if (val <= dom1) {
            J('input[name=' + i + '_' + q + ']').val()
          } else {
            J('input[name=' + i + '_' + q + ']').val('0.00')
          }
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 100%;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

</style>



